* {
  -webkit-box-sizing: inherit;
          box-sizing: inherit;
}
html,
body {
  margin: 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  font-family: "Roboto", sans-serif;
  font-weight: 500;
}

.header {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: stretch;
      justify-content: stretch;
  background-color: #888;
  color: white;
}
.header button {
  -ms-flex: 1 0;
      flex: 1 0;
  height: 40px;
  font-size: 24px;
  border: 3px solid rgba(0, 0, 0, 0.2);
  background: #fff;
  margin-right: 2px;
}
.header button:focus {
  color: darkcyan;
  font-weight: 700;
  border: 3px solid;
}
.header button:last-child {
  margin-right: 0;
  -ms-flex: 0 0;
      flex: 0 0;
}
.minutes {
  font-size: 0.8em;
  vertical-align: top;
}

.settings {
  font-size: 20px;
  padding-left: 8px;
}
.settings__group {
  margin-top: 16px;
}
.settings button {
  margin-left: 5px;
  font-size: 20px;
}
/* .lessons {
  
} */
.lesson {
  padding: 4px 4px 4px 8px;
  font-size: 20px;
}
.lesson--active {
  color: darkcyan;
}
.lesson:nth-child(even) {
  background-color: #ccc;
}

.lesson:nth-child(odd) {
  background-color: #ddd;
}

.class {
  font-weight: 400;
  font-size: 0.9em;
}



